<template>
<div>
  <h2></h2>
  <div class="content">
    <header class="top_bar">
      <p style="color:#b4b4b5"></p>
    </header>
    <div style="display: flex">
      <section style="width: 3.2rem;  margin-right: 0.2rem; margin-top: 0.2rem">
        <div class="left">
          <div class="left_h">
            <img :src="baseInfo.pic" alt="" style="width: 50px;max-height: 100%;border-radius: 50%">
          </div>
          <div class="left_c">
            <header>
              <p>{{name}}</p>
              <time class="font">201710</time>
            </header>
            <p v-for="(item,key) in selected">
              <span class="font">{{item.name}}</span>
              <strong>{{item.example}}</strong>
            </p>
          </div>
          <div class="left_f">
            <p class="font"></p>
            <Icon type="chevron-right" color="#b4b4b5"></Icon>
          </div>
        </div>
        <p style="text-align: center;margin: 0.2rem 0;width: 100%" class="font"></p>
      </section>
      <section class="right">
        <section style="display: flex;justify-content: space-between">
          <p></p>
          <p><span style="color:#b4b4b5 ;">?</span> <a href="javascript:void(0)"></a></p>
        </section>
        <section class="select">
          <div>
            <span class="search">
              <input type="text" placeholder="" class="input">
              <a href="javascript:void(0)"><Icon type="ios-search-strong" size="30" color="#a3a3a3"></Icon></a>
            </span>
          </div>
          <div class="select_c">
            <label class="selected" v-for="(item,key) in lists">
              <input type="checkbox" v-model="item.checkbox" @change="change(item,key)">
              <span>{{item.name}}</span>
            </label>
          </div>
        </section>
        <section class="selected_c">
          <p><span></span> <span style="color:#b4b4b5"></span></p>
          <div>
            <draggable v-model="selected" :move="getdata" @update="datadragEnd"  class="pre_menu_list">
              <label class="key_selected" v-for="(item,key) in selected">
                <div>
                  <input type="checkbox" v-model="item.checkbox"  @change="cancel(item,key)">
                  <span>{{item.name}}</span>
                </div>
                <Icon type="navicon" size="25"></Icon>
              </label>
            </draggable>
          </div>
        </section>
      </section>
    </div>
    <div class="footer_bar">
      <a href="javascript:void(0)" class="btn" @click="save"></a>
    </div>
  </div>
</div>
</template>
<script>
  import draggable from 'vuedraggable'
export default{
    data(){
        return{
          name:'',
          lists:[],
          selected:[],
          id:"",
          appid:"",
          baseInfo:''
        }
    },
  components:{draggable},
  methods:{
    save(){
        let nameList=[],idList=[];
          this.selected.forEach(item=>{
              nameList.push(item.name);
              idList.push(item.keyword_id);
          });
       this.post("custom/loop/template_add",{
           id:this.id,
            title:this.name,
            keyword_name_list:nameList.toString(),
            keyword_id_list:idList.toString()
       }).then(res=>{
         console.log(res);
          if(res.data.code===10000){
            let url=`/${this.appid}/editor/template`;
            this.$router.push(url);
          }else{

          }
       })
    },
    getdata(evt){
//        console.log(evt.draggedContext.element)
    },
    datadragEnd(evt){
//        console.log(this.selected);
    },
    change(val,key){
        if(this.selected.length<10){
          this.selected.push(val);
          this.selected=this.selected.filter(item=>{
            return item["checkbox"]===true;
          })
        }else{
          alert("10")
        }

    },
    cancel(val,key){
      this.selected.splice(key,1);
    }
  },
  mounted(){
      this.baseInfo=JSON.parse(sessionStorage.getItem("baseInfo"));
      this.$store.commit('setStatus',false);
      this.id=this.$route.params.id;
      this.appid=this.$route.params.appid;
        this.post("custom/loop/template_get",{id:this.id}).then(res=>{
           if(res.data.code===10000){
              this.name=res.data.data.title;
              this.lists=res.data.data.list;
              this.lists.forEach(item=>{
                  item["checkbox"]=false
              })
           }else{

           }
        })
  }
}
</script>
<style scoped>
  .font{  color:#b4b4b5 ;margin-right: 0.2rem;display: inline-block;width: 0.8rem}
.content{
  padding: 0.2rem;
}
.top_bar{
      padding: 0.2rem 0;
      border-bottom: 1px dashed #b4b4b5;
  }
.left{
  width: 3.2rem;
  /*padding: 0.1rem;*/
  border: 1px solid #f2f2f2;
}
.left_h{
  padding: 0.15rem 0.1rem;
  border-bottom: 1px solid #f2f2f2;
}
.left_c{
  padding: 0.1rem;
  min-height: 2.3rem;
}
.left_c>header{
  margin-bottom: 0.2rem;
}
.left_f{
  padding: 0.1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
  .right{
    flex: 1;
    margin-left: 0.2rem;
    margin-top: 0.2rem;
    padding: 0.3rem 0.2rem;
    background: #f6f8f9;
  }
  .select{
    margin-top: 0.1rem;
    background-color: #fff;
    border-radius: 0.04rem;
    padding: 0.05rem 0.2rem;
  }
  .search{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #e7e7eb;
  }
  .input{
    /*padd*/
    flex: 1;
  }
  .select_c{
    border-top-width: 0;
    padding-bottom: 20px;
    max-height: 150px;
    overflow-y: auto;
    margin-top: 0.1rem;
  }
  .selected{
    cursor: pointer;
    /*display: inline-block;*/
    width: 100%;
    padding: 0.1rem;
    display: flex;
    align-items: center;
  }
  .selected:hover{
    background-color: #f6f8f9;
  }
  .selected>input{
    display: inline-block;
    margin-right: 0.05rem;
  }
  .selected_c{
    margin: 0.2rem 0;
  }
  .key_selected{
    margin-top: 0.05rem;
    cursor: pointer;
    /*display: inline-block;*/
    width: 100%;
    padding: 0.1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
  }
  .key_selected input{
    display: inline-block;
    margin-right: 0.05rem;
  }
  .footer_bar{
    padding: 0.5rem 0;
    text-align: center;
  }
  .btn{
    color: white;
    border-radius: 0.05rem;
    padding: 0.1rem 0.4rem;
    background-color: #a5cd1c;
  }
</style>



